import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  background: dodgerblue;
  width: 100%;
  /* height: 100vh; */
  padding: 10% 0;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const SlideWrapper = styled.div`
  background: darkolivegreen;
  position: relative;
  width: 70%;
  height: 450px;
`;

const Slide = styled.div`
  background: salmon;
  width: 100%;
  height: auto;
  position: absolute;
  display: flex;
  flex-direction: column;
`;

const Img = styled.img`
  width: 100%;
  height: auto;
`;

const Desc = styled.div`
  background: indigo;
  font-size: 1.2rem;
  text-align: center;
`;

const Button = styled.div`
  width: 100px;
  height: 100px;
  background: seagreen;
`;

const Test = () => {
  return (
    <>
      <Wrapper>
        <Button></Button>
        <SlideWrapper>
          <Slide style={{ zIndex: 1 }}>
            <Img src="http://placehold.it/800x900"></Img>
            <Desc>a img</Desc>
          </Slide>
          <Slide>
            <Img src="http://placehold.it/800x900"></Img>
            <Desc>b img</Desc>
          </Slide>
          <Slide>
            <Img src="http://placehold.it/800x900"></Img>
            <Desc>c img</Desc>
          </Slide>
        </SlideWrapper>
        <Button></Button>
      </Wrapper>
    </>
  );
};

export default Test;
